import React, { useState, useEffect } from 'react';
import { Accordion } from 'react-bootstrap-accordion'
import ExploreItem from './ExploreItem';
import todayPickData from '../../../assets/fake-data/data-today-pick';
import SideBar from '../../../components/layouts/home-8/SideBar';
import { InputGroup, FormControl, Button, Dropdown, DropdownButton } from 'react-bootstrap';
import Select from '../../Common/Select';

const Explore = props => {
    const sideData = props.data;
    const [filterstring, setfilterstring] = useState('');
    const find = () => {
    }
    const [collectionSelect, SetCollectionSelect] = useState('Price: Low To High ');

    return (
        <section className="tf-explore tf-section pt-4">
            <div className="themesflat-container">
                <div className="row">
                    <div className="col-xl-3 col-lg-3 col-md-12">
                        <SideBar data={sideData}/>
                    </div>
                    
                    <div className="col-xl-9 col-lg-9 col-md-12">
                        <div className='widget-search mt-2'>
                            <InputGroup className="mb-3">
                                <FormControl
                                placeholder="Please type user's name or address"
                                aria-label="Recipient's username"
                                aria-describedby="basic-addon2"
                                value={filterstring}
                                onChange={(ev) => setfilterstring(ev.target.value)}
                                />
                                    <Button variant="outline-secondary" onClick={find}>
                                        <i className="fa fa-search" aria-hidden="true"></i>
                                    </Button>
                                    {/* <DropdownButton
                                    variant="outline-secondary"
                                    title="Dropdown"
                                    id="input-group-dropdown-2"
                                    align="end"
                                    >
                                        <Dropdown.Item>Price: Low to High</Dropdown.Item>
                                        <Dropdown.Item>Price: High to Low</Dropdown.Item>
                                    </DropdownButton> */}
                                    <div className="mx-4">
                                        <Select value={collectionSelect} setValue={SetCollectionSelect} options={['Price: Low To High ', 'Price: High To Low ']}/>  
                                    </div>
                                    <Button variant="outline-secondary" onClick={find}>
                                        <i className="fa fa-search" aria-hidden="true"></i>
                                    </Button>
                                </InputGroup>
                            </div>
                        <ExploreItem data={todayPickData} contract_id={props.contract_id}/>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Explore;
