import React , { useEffect, useState } from 'react';
import { Accordion } from 'react-bootstrap-accordion'
import { Scrollbar } from "react-scrollbars-custom";

const SideBar = props => {
    const data = props.data;
    const filters = [
        // {
        //     title: 'Categories',
        //     content: [
        //         {
        //             field: 'Art',
        //             checked: 'checked'
        //         },
        //         {
        //             field: 'Music',
        //             checked: ''
        //         },
        //         {
        //             field: 'Gif',
        //             checked: ''
        //         },
        //         {
        //             field: 'Video',
        //             checked: ''
        //         },
        //     ]
        // },
        // {
        //     title: 'Price Filter',
        //     content: [
        //         {
        //             field: 'Low - High',
        //             checked: 'checked'
        //         },
        //         {
        //             field: 'High - Low',
        //             checked: ''
        //         },
        //     ]
        // }
    ]

    const [dataCate, SetDataCate] = useState(
        []
    )

    useEffect(() => {
        let attributes = []
        Object.entries(data).forEach(([key, value]) => {
            let contents = []
            Object.entries(value).forEach(([key1, value1]) => {
                contents.push({field: `${key1} (${value1.count})`, checked: ''})
            });
            attributes.push({title: key, content: contents})
        });
        SetDataCate([...filters, ...attributes])
    
    }, [data]);

  return (
    <div id="side-bar" className="side-bar style-3 item">
        <div className="widget widget-filter style-1 mgbt-0">
            <div className="header-widget-filter">
                <h4 className="title-widget">Filter</h4>
            </div>
        </div>     
        <div className="divider"></div>
        <Scrollbar style={{height: 750 }}>
            <div className="wrap-category">
                {
                    dataCate.map((item,index)=> (
                        <div key={index} className="widget widget-category boder-bt">
                            <Accordion title={item.title} show={true}>
                                <form action="#">
                                    {
                                        item.content.map((itemm , index) => (
                                            <div key={index}>
                                                <label>
                                                    <span>{itemm.field}</span>
                                                    <span className="pst-re">
                                                        <input type="checkbox" defaultChecked={itemm.checked} />
                                                        <span className="btn-checkbox"></span>
                                                    </span>
                                                </label><br/>
                                            </div>
                                        ))
                                    }                                            
                                </form>
                            </Accordion>
                        </div>
                    ))
                }
            </div>
        </Scrollbar>
    </div>
  );
}

export default SideBar;